.footer{
    &-top{
        border-bottom: 1px solid #D8DFE2;
        border-top: 1px solid #D8DFE2;
        padding: 20px 0;
        .container > div{
            display: flex;
            align-items: center;
            span{
                margin-left: 40px;
            }
        }
    }
    .container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }
    &-footersite{
        a{
            color: #F74B4B;
            &:hover{
                text-decoration: underline;
            }
        }
    }
    &-bottom{
        padding: 47px 0 70px;
    }
    &__text{
        max-width: 670px;
        font-size: 13px;
        line-height: 160%;
        color: #3E3E3E;
        opacity: 0.85;
    }
    &__img{
        max-width: 440px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        .img{
           width: 33.33%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 20px;
        }
    }
}